export const getSpPath = (suit: string, count: number) => {
    let lastLetter: string = ''
    switch (suit) {
        case 'HongTao':
            lastLetter = 'H'
            break;
        case 'HeiTao':
            lastLetter = 'S'
            break;
        case 'MeiHua':
            lastLetter = 'C'
            break;
        case 'FangKuai':
            lastLetter = 'D'
            break;
    }

    return `${suit}/${count}${lastLetter}`
}